import { notFound } from "next/navigation";
import { prisma } from "@/lib/prisma";

interface PageProps {
  params: { kode: string };
}

export default async function UndanganPage({ params }: PageProps) {
  const tamu = await prisma.tamu.findUnique({
    where: { kode: params.kode },
  });

  if (!tamu) return notFound();

  return (
    <div className="min-h-screen flex flex-col items-center justify-center">
      <h1 className="text-2xl font-bold mb-2">Undangan Pernikahan</h1>
      <p>Nama: {tamu.nama}</p>
      <p>Alamat: {tamu.alamat}</p>
    </div>
  );
}
